import moment from "moment-timezone";
import { DailyWeather, HourlyWeather, Weather } from "../types/Weather";
import date from 'date-and-time';

export const getDateOnlyString = (isoDateString: string | undefined): string | undefined => {
  if (!isoDateString) {
    return undefined;
  }
  const dateObj = new Date(isoDateString);
  return date.format(dateObj, 'YYYY-MM-DD');
}

export const getNextDateOnlyString = (isoDateString: string | undefined): string | undefined => {
  if (!isoDateString) {
    return undefined;
  }
  const dateObj = new Date(isoDateString);
  const nextDay = date.addDays(dateObj, 1);
  return date.format(nextDay, 'YYYY-MM-DD');
}

export const getDateAndDayOfWeek = (dateOnlyString: string): string => {
  const dateObj = date.parse(dateOnlyString, 'YYYY-MM-DD');
  return date.format(dateObj, "dddd, MMM D");
}

export const getHourlyList = (weather: Weather | null): Map<string, HourlyWeather[]> => {
  if (!weather) {
    return new Map<string, HourlyWeather[]>();
  }

  const currentTime = weather.current_weather.time;
  const hourlyWeatherMap = new Map<string, HourlyWeather[]>();
  weather.hourly.time
    .forEach((eachTime, i) => {
      if (eachTime >= currentTime) {
        const dateOnlyStr = getDateOnlyString(eachTime);
        if (dateOnlyStr) {
          let hourlyWeatherList = hourlyWeatherMap.get(dateOnlyStr);
          if (!hourlyWeatherList) {
            hourlyWeatherList = [];
            hourlyWeatherMap.set(dateOnlyStr, hourlyWeatherList);
          }
          hourlyWeatherList.push({
            time: eachTime,
            temperature_2m: weather.hourly.temperature_2m[i],
            apparent_temperature: weather.hourly.apparent_temperature[i],
            relativehumidity_2m: weather.hourly.relativehumidity_2m[i],
            precipitation_probability: weather.hourly.precipitation_probability[i],
            rain: weather.hourly.rain[i],
            showers: weather.hourly.showers[i],
            snowfall: weather.hourly.snowfall[i],
            weathercode: weather.hourly.weathercode[i],
            visibility: weather.hourly.visibility[i],
            is_day: weather.hourly.is_day[i],
          });
        }
      }
    });
  return hourlyWeatherMap;
}

export const getDailyList = (weather: Weather | null): Map<string, DailyWeather> => {
  if (!weather) {
    return new Map<string, DailyWeather>();
  }

  const dailyWeatherMap = new Map<string, DailyWeather>();
  weather.daily.time
    .forEach((eachTime, i) => {
      dailyWeatherMap.set(eachTime, {
        time: eachTime,
        weathercode: weather.daily.weathercode[i],
        temperature_2m_max: weather.daily.temperature_2m_max[i],
        temperature_2m_min: weather.daily.temperature_2m_min[i],
        uv_index_max: weather.daily.uv_index_max[i],
        precipitation_probability_mean: weather.daily.precipitation_probability_mean[i],
      });
    });
  return dailyWeatherMap;
}


export const getTimeOnlyString = (isoDateString: string): string | undefined => {
  if (!isoDateString) {
    return undefined;
  }
  const dateObj = new Date(isoDateString);
  return date.format(dateObj, 'hA');
}

export const getCurrentFormattedDateTime = (): string => {
  const dateObj = new Date();
  return date.format(dateObj, 'MMM D, hh:mm A');
}

export const convertMetersToKilometers = (meters: number): string => {
  const km = meters / 1000;
  return km.toFixed(0);
}

export const getLocalTimeString = (isoDateString: string, timezone: string): string => {
  const dateMoment = moment(isoDateString);
  const timezoneStr = dateMoment.tz(timezone).format('z');

  const localDate = new Date(new Date(isoDateString).toLocaleString("en-US", { timeZone: timezone }));
  const localDateString = date.format(localDate, 'MMM DD - hh:mm A')
  return `${localDateString} (${timezoneStr})`;
}