import { DegreeType } from "../types/Weather";

const DEGREE_TYPE_KEY = "degreeType";

export const getDegreeTypeInStorage = (): DegreeType => {
  return localStorage.getItem(DEGREE_TYPE_KEY) === 'fahrenheit' ? 'fahrenheit' : 'celsius';
}

export const setDegreeTypeInStorage = (degreeType: DegreeType) => {
  localStorage.setItem(DEGREE_TYPE_KEY, degreeType);
}