import React, { useContext, useEffect, useState } from "react";
import useDeviceLocation from "../hooks/useDeviceLocation";
import { GeoLocationByPlace } from "../types/GeoLocation";
import { getWeather, getWeatherAlertsByCity } from "../services/weatherAndLocationService";
import { CurrentWeather, DailyWeather, DailyWeatherUnit, DegreeType, HourlyWeather, HourlyWeatherUnit, Weather, WeatherAlert } from "../types/Weather";
import { getDailyList, getHourlyList } from "../helpers/weather-helpers";
import { getDegreeTypeInStorage } from "../services/localStorageService";

interface LocationWeatherContextData {
  isLoading: boolean,
  deviceLocationErrMsg: string | null,
  geocodingBaseUrl: string | undefined,
  isUseDeviceLocation: boolean,
  setIsUseDeviceLocation: (isUseDeviceLocation: boolean) => void,
  setSelectedGeoLocation: (location: GeoLocationByPlace) => void,
  weather: Weather | null,
  hourlyWeatherMap: Map<string, HourlyWeather[]>,
  hourlyWeatherUnit: HourlyWeatherUnit | undefined,
  dailyWeatherMap: Map<string, DailyWeather>,
  dailyWeatherUnit: DailyWeatherUnit | undefined,
  currentWeather: CurrentWeather | undefined,
  setDegreeType: (degreeType: DegreeType) => void,
  weatherAlerts: WeatherAlert[],
};

const GEOCODING_BASE_URL = process.env.REACT_APP_GEOCODING_BASE_URL;
const WEATHER_BASE_URL = process.env.REACT_APP_WEATHER_BASE_URL;
const IPAPI_BASE_URL = process.env.REACT_APP_IPAPI_BASE_URL;
const WEATHER_ALERTS_URL = process.env.REACT_APP_WEATHER_ALERTS_URL;

export const LocationWeatherContext = React.createContext<LocationWeatherContextData>({
  isLoading: false,
  deviceLocationErrMsg: null,
  geocodingBaseUrl: GEOCODING_BASE_URL,
  isUseDeviceLocation: true,
  setIsUseDeviceLocation: (isUseDeviceLocation: boolean) => { },
  setSelectedGeoLocation: (location: GeoLocationByPlace) => { },
  weather: null,
  hourlyWeatherMap: new Map<string, HourlyWeather[]>(),
  hourlyWeatherUnit: undefined,
  dailyWeatherMap: new Map<string, DailyWeather>(),
  dailyWeatherUnit: undefined,
  currentWeather: undefined,
  setDegreeType: () => { },
  weatherAlerts: [],
});

const LocationWeatherProvider = ({ children }: { children: JSX.Element }) => {
  const [isUseDeviceLocation, setIsUseDeviceLocation] = useState(true);
  const [selectedGeoLocation, setSelectedGeoLocation] = useState<GeoLocationByPlace>();
  const [weather, setWeather] = useState<Weather | null>(null);
  const [hourlyWeatherMap, setHourlyWeatherMap] = useState<Map<string, HourlyWeather[]>>(new Map());
  const [hourlyWeatherUnit, setHourlyWeatherUnit] = useState<HourlyWeatherUnit>();
  const [dailyWeatherMap, setDailyWeatherMap] = useState<Map<string, DailyWeather>>(new Map());
  const [dailyWeatherUnit, setDailyWeatherUnit] = useState<DailyWeatherUnit>();
  const [currentWeather, setCurrentWeather] = useState<CurrentWeather>();
  const [degreeType, setDegreeType] = useState<DegreeType>('celsius');
  const [isPending, setIsPending] = useState(false);
  const [weatherAlerts, setWeatherAlerts] = useState<WeatherAlert[]>([]);

  const { lat: deviceLat, long: deviceLong, city, country, errMsg } = useDeviceLocation(IPAPI_BASE_URL!);
  const weatherLat = isUseDeviceLocation ? deviceLat : selectedGeoLocation?.latitude;
  const weatherLong = isUseDeviceLocation ? deviceLong : selectedGeoLocation?.longitude;
  const weatherCity = isUseDeviceLocation ? city : selectedGeoLocation?.name;
  const weatherCountry = isUseDeviceLocation ? country : selectedGeoLocation?.country;

  useEffect(() => {
    if (!weatherCity || !weatherCountry) {
      return;
    }

    setIsPending(true);
    getWeatherAlertsByCity(weatherCity, weatherCountry, WEATHER_ALERTS_URL!)
      .then((weatherAlerts) => {
        setWeatherAlerts(weatherAlerts);
      })
      .catch((err) => {
        console.error(err);
      })
      .finally(() => {
        setIsPending(false);
      });
  }, [weatherCity, weatherCountry]);


  useEffect(() => {
    setIsPending(true);
    getWeather(
      weatherLat,
      weatherLong,
      getDegreeTypeInStorage(),
      WEATHER_BASE_URL
    )
      .then((weather) => {
        setWeather(weather);
        const hourlyWeather = getHourlyList(weather);
        const dailyWeather = getDailyList(weather);
        setHourlyWeatherMap(hourlyWeather);
        setHourlyWeatherUnit(weather?.hourly_units);
        setDailyWeatherMap(dailyWeather);
        setDailyWeatherUnit(weather?.daily_units);
        setCurrentWeather(weather?.current_weather);
      })
      .catch((err) => {
        console.error(err);
      })
      .finally(() => {
        setIsPending(false);
      })
  }, [weatherLat, weatherLong, degreeType]);

  return (
    <LocationWeatherContext.Provider value={{
      isLoading: isPending,
      deviceLocationErrMsg: errMsg,
      geocodingBaseUrl: GEOCODING_BASE_URL,
      isUseDeviceLocation,
      setIsUseDeviceLocation,
      setSelectedGeoLocation,
      weather,
      hourlyWeatherMap,
      hourlyWeatherUnit,
      dailyWeatherMap,
      dailyWeatherUnit,
      currentWeather,
      setDegreeType,
      weatherAlerts,
    }}>
      {children}
    </LocationWeatherContext.Provider>

  );
}

export default LocationWeatherProvider;
export const useLocationWeatherContext = () => useContext(LocationWeatherContext);